import React, { Component } from "react";
// import moment from 'moment';
import "./Timer.css";

let x = null;
let deadline = null;

type MyState = {
    days: any
    minutes: any
    hours: any
    seconds: any
};

class CountDown extends Component<unknown, MyState> {
    constructor(props) {
        super(props);
        this.count = this.count.bind(this);
        this.state = {
            days: 0,
            minutes: 0,
            hours: 0,
            seconds: 0,
        };

    }

    componentDidMount() {
        deadline = new Date("Nov 27, 2021 23:59:59").getTime();
        x = setInterval(this.count, 1000);
    }

    count() {
        const now = new Date().getTime();
        const t = deadline - now;
        const dd = Math.floor(t / (1000 * 60 * 60 * 24));
        const hh = Math.floor((t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const mm = Math.floor((t % (1000 * 60 * 60)) / (1000 * 60));
        const ss = Math.floor((t % (1000 * 60)) / 1000);

        const days = dd.toString().padStart(2, '0');
        const hours = hh.toString().padStart(2, '0');
        const minutes = mm.toString().padStart(2, '0');
        const seconds = ss.toString().padStart(2, '0');

        this.setState({ days, minutes, hours, seconds });

        if (t < 0) {
            clearInterval(x);
            this.setState({
                days: 0,
                minutes: 0,
                hours: 0,
                seconds: 0,
            });
        }
    }

    render() {
        const { days, seconds, hours, minutes } = this.state;
        return (
            <div id="countdown">
                <div className="col-4">
                    <div className="box">
                        <p id="day">{days}</p>
                        <span className="text">Days</span>
                    </div>
                </div>
                <div className="col-4">
                    <div className="box">
                        <p id="hour">{hours}</p>
                        <span className="text">Hours</span>
                    </div>
                </div>
                <div className="col-4">
                    <div className="box">
                        <p id="minute">{minutes}</p>
                        <span className="text">Minutes</span>
                    </div>
                </div>
                <div className="col-4">
                    <div className="box">
                        <p id="second">{seconds}</p>
                        <span className="text">Seconds</span>
                    </div>
                </div>
            </div>
        );
    }
}

export default CountDown;
