const IfoFoldableCard: React.FC<IfoFoldableCardProps> = ({ ifo, isInitiallyVisible }) => {
  const [isVisible, setIsVisible] = useState(isInitiallyVisible)
  const [enableStatus, setEnableStatus] = useState(EnableStatus.DISABLED)
  const [showBNBInput, setShowBNBInput] = useState(false)
  const [showMUFTInput, setShowMUFTInput] = useState(false)
  const [showMSWAPInput, setShowMSWAPInput] = useState(false)
  const [BNBValue, setBNBValue] = useState('')
  const [MUFTValue, setMUFTValue] = useState('')
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const raisingTokenContract = useERC20(ifo.currency.address)
  const iloContract = useILOContract(iloContractAddress)
  const iloApproveContract = useILOApproveContract(muftContractAddress)
  // const Ribbon = getRibbonComponent(ifo, publicIfoData.status, t)
  // const isActive = publicIfoData.status !== 'finished' && ifo.isActive
  // const { contract } = walletIfoData
  // const onApprove = useIfoApprove(raisingTokenContract, contract.address)
  // const { toastSuccess } = useToast()

  // ILO section calc
  const [isLoading, setIsLoading] = useState(false)
  const [approved, setApproved] = useState(false)
  const [committedMuft, setCommittedMuft] = useState('0.00')
  const [committedBnb, setCommittedBnb] = useState('0.00')
  const [committedMuftSwap, setCommittedMuftSwap] = useState('0.00')
  const [totalBnb, setTotalBnb] = useState(0)
  const [totalMuft, setTotalMuft] = useState(0)
  const [bnbApproved, setBnbApproved] = useState(false)
  const [presaleEnded, setPresaleEnded] = useState(true)
  const [bnbOverMax, setBnbOverMax] = useState(false)
  const [muftOverMax, setMuftOverMax] = useState(false)
  const { balance: muftBalance, fetchStatus: cakeFetchStatus } = useTokenBalance(tokens.muft.address)
  const { balance, fetchStatus } = useGetBnbBalance()
  const { toastSuccess, toastError } = useToast()
  let myTimer

  // const handleApprove = async () => {
  //   try {
  //     setEnableStatus(EnableStatus.IS_ENABLING)

  //     const receipt = await onApprove()

  //     setEnableStatus(EnableStatus.ENABLED)
  //     toastSuccess(
  //       t('Successfully Enabled!'),
  //       <ToastDescriptionWithTx txHash={receipt.transactionHash}>
  //         {t('You can now participate in the %symbol% IFO.', { symbol: ifo.token.symbol })}
  //       </ToastDescriptionWithTx>,
  //     )
  //   } catch (error) {
  //     setEnableStatus(EnableStatus.DISABLED)
  //   }
  // }

  const sendMUFTApprove = async () => {
    // console.log('...', String(parseFloat(MUFTValue) * 10 ** 18))
    // console.log('...==...', utils.parseEther(`${MUFTValue}`))
    // console.log('...==...=...', utils.parseEther(`${MUFTValue}`).toString())
    if (MUFTValue !== null && parseFloat(MUFTValue) > 0) {
      try {
        setIsLoading(true)
        await iloApproveContract.approve(iloContractAddress, utils.parseEther(`${MUFTValue}`).toString())
        setTimeout(() => {
          setApproved(true)
          setIsLoading(false)
        }, 3000);
      } catch (error) {
        setApproved(false)
        setIsLoading(false)
        console.log(error)
      }
    } else alert("Please enter MUFT amount")
  }
  const sendMUFTConfirm = async () => {
    if (MUFTValue !== null && parseFloat(MUFTValue) > 0) {
      try {
        setIsLoading(true)
        await iloContract.commitMuft(utils.parseEther(`${MUFTValue}`).toString())
        setApproved(false)
        setIsLoading(false)
        setMUFTValue('')
        setShowMUFTInput(false)
        toastSuccess(
          `${t('MUFT committed Successfully')}!`,
          t('Your browser will reload once the transaction is successful', { symbol: 'CAKE' }),
        )
        setTimeout(() => {
          calcBalance()
          window.location.reload();
        }, 20000);
      } catch (error) {
        setIsLoading(false)
        setApproved(false)
        console.log('sendMUFTConfirm error', error)
      }
    } else alert("Please enter MUFT amount")

  }
  const calcBalance = async () => {
    try {
      const temptotalBnb = await iloContract.bnb_total()
      const temptotalMuft = await iloContract.muft_total()
      const tempCommitted = await iloContract.getUserInfo(account)
      if (temptotalBnb !== null && temptotalBnb !== undefined
        && temptotalMuft !== null && temptotalMuft !== undefined
        && tempCommitted.muft !== undefined && tempCommitted.muft !== null
        && tempCommitted.bnb !== undefined && tempCommitted.bnb !== null
        && tempCommitted.muft_swap !== undefined && tempCommitted.muft_swap !== null) {
        setCommittedMuft(String(utils.formatEther(tempCommitted.muft)))
        setCommittedBnb(String(utils.formatEther(tempCommitted.bnb)))
        setCommittedMuftSwap(String(utils.formatEther(tempCommitted.muft_swap)))
        setTotalBnb(parseFloat(utils.formatEther(temptotalBnb.toString())))
        setTotalMuft(parseFloat(utils.formatEther(temptotalMuft.toString())))
      }
    } catch (error) {
      console.log('calcBalance err', error)
    }
  }

  useEffect(() => { // get committed MUFT & BNB & MSWAP value
    setTimeout(() => {
      calcBalance()
    }, 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [account, showMUFTInput, showBNBInput, iloContract, isLoading])

  const presaleSensor = () => {
    const deadline = new Date("Nov 27, 2021 23:59:59").getTime();
    const now = new Date().getTime();
    const diff = deadline - now;
    if (diff < 0) {
      setPresaleEnded(true)
      setShowMUFTInput(false)
      setShowBNBInput(false)
      clearInterval(myTimer)
    }
  }
  useEffect(() => {
    const deadline = new Date("Nov 27, 2021 23:59:59").getTime();
    const now = new Date().getTime();
    const diff = deadline - now;
    if (diff > 0) {
      setPresaleEnded(false)
      // eslint-disable-next-line react-hooks/exhaustive-deps
      myTimer = setInterval(presaleSensor, 1000);
    }
  }, [])

  // useEffect(() => {
  //   const checkAllowance = async () => {
  //     try {
  //       const response = await raisingTokenContract.allowance(account, contract.address)
  //       const currentAllowance = new BigNumber(response.toString())
  //       setEnableStatus(currentAllowance.lte(0) ? EnableStatus.DISABLED : EnableStatus.ENABLED)
  //     } catch (error) {
  //       setEnableStatus(EnableStatus.DISABLED)
  //     }
  //   }

  //   if (account) {
  //     checkAllowance()
  //   }
  // }, [account, raisingTokenContract, contract, setEnableStatus])

  const CommitBNB = () => {
    setShowBNBInput(true)
    calcBalance()
  }
  const CommitMUFT = () => {
    setApproved(false)
    setShowMUFTInput(true)
    calcBalance()
  }
  const approveBNB = () => {
    if (BNBValue !== null && parseFloat(BNBValue) > 0) {
      setIsLoading(true)
      setTimeout(() => {
        setIsLoading(false)
        setBnbApproved(true)
      }, 1000);
    } else alert("Please enter BNB amount")
  }

  const confirmBNB = async () => {

    try {
      setIsLoading(true)
      await iloContract.commitBNB({ value: String(parseFloat(BNBValue) * 10 ** 18) })
      setIsLoading(false)
      setBNBValue(null)
      setShowBNBInput(false)
      toastSuccess(
        `${t('BNB committed Successfully')}!`,
        t('Your browser will reload in less than 1 min!', { symbol: 'CAKE' }),
      )
      setTimeout(() => {
        window.location.reload();
        calcBalance()
      }, 20000);
    } catch (error) {
      setIsLoading(false)
      console.log('sendBNBConfirm error', error)
    }

  }

  const confirmMSWAP = async () => {
    if (window.confirm()) {
      try {
        setIsLoading(true)
        await iloContract.withdrawMuftSwap(utils.parseEther(`${committedMuftSwap}`).toString())
        setIsLoading(false)
        setTimeout(() => {
          // calcBalance()
          window.location.reload();
        }, 10000);
      } catch (error) {
        setIsLoading(false)
        console.log('withdrawMswap error', error)
      }
    }
  }

  const setBNBVal = (e: any) => {
    setBNBValue(e.target.value)
    if (parseFloat(e.target.value) > parseFloat(formatBigNumber(balance, 6).toString()) * 0.99) {
      setBnbOverMax(true)
    } else setBnbOverMax(false)
  }
  const setMUFTVal = (e: any) => {
    setMUFTValue(e.target.value)
    if (parseFloat(e.target.value) > parseFloat(getFullDisplayBalance(muftBalance, 18, 3)) * 0.99) {
      setMuftOverMax(true)
    } else setMuftOverMax(false)
  }

  function numberWithCommas(x: number) {
    return x.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  }

  const getMaxBnb = () => {
    const temp = parseFloat(formatBigNumber(balance, 6).toString()) * 0.99
    setBNBValue(temp.toString())
  }

  const getMaxMuft = () => {
    const temp = parseFloat(getFullDisplayBalance(muftBalance, 18, 3)) * 0.99
    setMUFTValue(temp.toString())
  }

  const displayOverMaxError = () => {
    return <OverMaxErr>MAX value should be less than 99% of your balance</OverMaxErr>
  }


  return (
    <StyledCard>
      {isLoading && <ConfirmLoader />}
      <Header ifoId={ifo.id}>
        <ExpandableButton expanded={isVisible} onClick={() => setIsVisible((prev) => !prev)} />
        <MswapTitle>MUFTSWAP</MswapTitle>
      </Header>
      <FoldableContent isVisible={isVisible} isActive={false}>
        <StyledCardBody>
          {/* {isActive && <Timer publicIfoData={publicIfoData} />}
          <CardsWrapper singleCard={!publicIfoData.poolBasic || !walletIfoData.poolBasic}>
            {publicIfoData.poolBasic && walletIfoData.poolBasic && (
              <IfoPoolCard
                poolId={PoolIds.poolBasic}
                ifo={ifo}
                publicIfoData={publicIfoData}
                walletIfoData={walletIfoData}
                onApprove={handleApprove}
                enableStatus={enableStatus}
              />
            )}
            <IfoPoolCard
              poolId={PoolIds.poolUnlimited}
              ifo={ifo}
              publicIfoData={publicIfoData}
              walletIfoData={walletIfoData}
              onApprove={handleApprove}
              enableStatus={enableStatus}
            />
          </CardsWrapper>
          <Achievement ifo={ifo} publicIfoData={publicIfoData} /> */}
          <ILOCard>
            <ILOHeader>
              <Text bold fontSize="22px" color="#c70101">{t('Limited Sale')}</Text>
            </ILOHeader>
            <ILOBody>
              <CoinContainer>
                <InfoContainer>
                  <img src={bnbImg} alt="bnbImg" />
                  <div>
                    <Text color="black" bold>{t('Your BNB Committed')}</Text>
                    <Text bold fontSize="16px" color="#C70101">{committedBnb}</Text>
                  </div>
                </InfoContainer>
                {account ?
                  <Button variant="switchbutton" disabled={presaleEnded} onClick={() => CommitBNB()} width="100%">
                    {t('Commit BNB')}
                  </Button>
                  :
                  <ConnectWalletButton width="100%" />
                }

                {showBNBInput &&
                  <>
                    <InputRow>
                      <StyledView>
                        <p>Balance: {formatBigNumber(balance, 6)}</p>
                        <InputWrapper>
                          <StyledInput
                            value={BNBValue}
                            onChange={(e) => setBNBVal(e)}
                            placeholder="0.0"
                            type="number"
                          />
                          <MaxButton onClick={getMaxBnb} aria-hidden="true">MAX</MaxButton>
                        </InputWrapper>
                      </StyledView>
                      <div>
                        <Button variant="switchbutton" disabled={bnbApproved || bnbOverMax} onClick={() => approveBNB()} width="40%">
                          {t('Approve')}
                        </Button>
                        <Button variant="switchbutton" disabled={!bnbApproved} onClick={() => confirmBNB()} width="40%">
                          {t('Confirm')}
                        </Button>
                      </div>
                    </InputRow>
                    {bnbOverMax && displayOverMaxError()}
                  </>
                }
              </CoinContainer>
              <CoinContainer>
                <InfoContainer>
                  <img src={muftImg} alt="muftImg" />
                  <div>
                    <Text color="black" bold>{t('Your MUFT Committed')}</Text>
                    <Text bold fontSize="16px" color="#C70101">{numberWithCommas(parseFloat(committedMuft))}</Text>
                  </div>
                </InfoContainer>
                {account ?
                  <Button variant="switchbutton" disabled={presaleEnded} onClick={() => CommitMUFT()} width="100%">
                    {t('Commit MUFT')}
                  </Button>
                  :
                  <ConnectWalletButton width="100%" />
                }

                {showMUFTInput &&
                  <>
                    <InputRow>
                      <StyledView>
                        <p>Balance: {getFullDisplayBalance(muftBalance, 18, 3)}</p>
                        <InputWrapper>
                          <StyledInput
                            value={MUFTValue}
                            // value={getFullDisplayBalance(muftBalance, 18, 3)}
                            onChange={(e) => setMUFTVal(e)}
                            placeholder="0.0"
                            type="number"
                          />
                          <MaxButton onClick={getMaxMuft} aria-hidden="true">MAX</MaxButton>
                        </InputWrapper>
                      </StyledView>
                      <div>
                        <Button variant="switchbutton" disabled={approved || muftOverMax} onClick={() => sendMUFTApprove()} width="40%">
                          {t('Approve')}
                        </Button>
                        <Button variant="switchbutton" disabled={!approved} onClick={() => sendMUFTConfirm()} width="40%">
                          {t('Confirm')}
                        </Button>
                      </div>
                    </InputRow>
                    {muftOverMax && displayOverMaxError()}
                  </>
                }
              </CoinContainer>
              <CoinContainer>
                <InfoContainer>
                  <img src={mswap} alt="mswap" />
                  <div>
                    <Text color="black" bold>{t('Your MSWAP to be RECEIVED')}</Text>
                    <Text bold fontSize="16px" color="#C70101">{numberWithCommas(parseFloat(parseFloat(committedMuftSwap)?.toFixed(6)))}</Text>
                  </div>
                </InfoContainer>
                {account ?
                  <Button variant="switchbutton" disabled={!presaleEnded} onClick={() => confirmMSWAP()} width="100%">
                    {t('Collect')}
                  </Button>
                  :
                  <ConnectWalletButton width="100%" />
                }

                {/* {showMSWAPInput &&
                  <InputRow>
                    <StyledInput
                      value={MSWAPValue}
                      onChange={(e) => setMSWAPVal(e)}
                      placeholder="0.0"
                      type="number"
                    />
                    <Button variant="switchbutton" onClick={() => confirmMSWAP()} width="40%">
                      {t('Confirm')}
                    </Button>
                  </InputRow>
                } */}
              </CoinContainer>
              <CoinContainer>
                <TextRow>
                  <Text color="black" fontSize="24px" textAlign="right" width="100%" bold>BNB</Text>
                </TextRow>
                <TextRow>
                  <InfoTitle>Total Committed</InfoTitle>
                  <InfoValue>{totalBnb} BNB</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Tokens Offered</InfoTitle>
                  <InfoValue>10,000,000 MSWAP</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>PreSale Price 10% OFF </InfoTitle>
                  <InfoValue>1 MSWAP = 0.000054</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Hard Cap</InfoTitle>
                  <InfoValue>600 BNB ({(totalBnb * 100 / 600).toFixed(6)}%)</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Soft Cap</InfoTitle>
                  <InfoValue>60 BNB ({(totalBnb * 100 / 60).toFixed(6)}%)</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Max allocation per person</InfoTitle>
                  <InfoValue>2 BNB</InfoValue>
                </TextRow>
              </CoinContainer>

              <CoinContainer>
                <TextRow>
                  <Text color="black" fontSize="24px" textAlign="right" width="100%" bold>MUFT</Text>
                </TextRow>
                <TextRow>
                  <InfoTitle>Total Committed</InfoTitle>
                  <InfoValue>{numberWithCommas(totalMuft)} MUFT </InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Tokens Offered</InfoTitle>
                  <InfoValue>1,000,000 MSWAP</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>PreSale Price 10% OFF </InfoTitle>
                  <InfoValue>1 MSWAP = 12,218</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Hard Cap</InfoTitle>
                  <InfoValue>10,000,000,000 MUFT ({(totalMuft * 100 / 10000000000).toFixed(6)}%)</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Soft Cap</InfoTitle>
                  <InfoValue>2,500,000,000 MUFT ({(totalMuft * 100 / 2500000000).toFixed(6)}%)</InfoValue>
                </TextRow>
                <TextRow>
                  <InfoTitle>Max allocation per person</InfoTitle>
                  <InfoValue>250,000,000</InfoValue>
                </TextRow>
              </CoinContainer>

            </ILOBody>
          </ILOCard>
        </StyledCardBody>
        <StyledCardFooter>
          <Button variant="switchbutton" endIcon={<ChevronUpIcon color="white" />} onClick={() => setIsVisible(false)}>
            {t('Close')}
          </Button>
        </StyledCardFooter>
      </FoldableContent>
    </StyledCard>
  )
}

export default IfoFoldableCard
