import React from 'react'
import styled from 'styled-components'
import { Text } from '@pancakeswap/uikit'
import CopyAddress from 'components/Menu/UserMenu/CopyAddress'
import { useTranslation } from 'contexts/Localization'
import { buyMuftUrl } from 'config/constants/endpoints'
import {
    muftswapContractAddress,
    muftContractAddress,
    iloContractAddress
} from '../constants/contratAddresses'

const AddressContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`
const SubContainer = styled.div`
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 500px;
    justify-content: space-between;
    margin: 0.5em 0;
    @media screen and (max-width: 530px) {
        flex-direction: column;
    }
`
const BuyMuftButton = styled.div`
    width: 150px;
    height: 44px;
    border-radius: 10px;
    background-color: #C70101;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 0.5em;
    cursor: pointer;
    &:hover {
        opacity: 0.85;
    }
`

const ContractAddresses = () => {
    const { t } = useTranslation()
    const buyMuft = () => {
        const newWindow = window.open(buyMuftUrl, '_blank', 'noopener,noreferrer')
        if (newWindow) newWindow.opener = null
    }
    return (
        <AddressContainer>
            <SubContainer>
                <Text color="white" bold>{t('MSWAP Contract Address: ')}</Text>
                <CopyAddress account={muftswapContractAddress} />
            </SubContainer>
            <SubContainer>
                <Text color="white" bold>{t('MUFT Contract Address: ')}</Text>
                <CopyAddress account={muftContractAddress} />
            </SubContainer>
            <BuyMuftButton onClick={buyMuft}>Buy MUFT</BuyMuftButton>
        </AddressContainer>
    )
}

export default ContractAddresses
